//
//
import {AnyAction} from "redux";

let $lang = localStorage.getItem("lang")||navigator.language

//
let setLang = (val:string) => {
    if(val===$lang) return;
    localStorage.setItem("lang", val)
}

export const lang = (state = $lang, action: AnyAction) => {
    switch (action.type) {
        case "lang":
            setLang(action.value)
            return action.value
        default :
            setLang(state)
            return state
    }
};
