import {Tag, Space, Divider, Modal, Row, Col, Radio, Tooltip, Form, Input,notification} from 'antd';
import {useState} from "react";
const { Search } = Input;
const {CheckableTag} = Tag;



const onSearch = (value:any) => console.log(value);

export default function Profiles(props: any) {
    const [isModalUserVisible, setIsModalUserVisible] = useState(false);
    const [isModalPhoneVisible, setIsModalPhoneVisible] = useState(false);
    const [value, setValue] = useState(1);

    const onChange = (e: any) => {
        console.log('radio checked', e.target.value);
        setValue(e.target.value);
    };

    const handleOk = () => {
        setIsModalUserVisible(false);
        setIsModalPhoneVisible(false);
    };

    const handleNext = () => {
        notification.error({
            message: 'code not found',
        });
    };

    const handleCancel = () => {
        setIsModalUserVisible(false);
        setIsModalPhoneVisible(false);
    };

    const onFinish = (values: any) => {
        console.log('Success:', values);
    };

    const onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };
    return <>
        <div className={'profile-container'}>

            <Row>
                <Col span={12}>
                    <Space direction={'vertical'}>
                        <img width='100px' height='100px'
                             src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" alt=""/>
                        <p>username</p>
                    </Space>
                </Col>
                <Col span={12}>
                    <Space direction={'vertical'}>
                        <Space>
                            
                            <CheckableTag
                                checked={isModalUserVisible}
                                onChange={setIsModalUserVisible}
                            >
                            </CheckableTag>
                        </Space>
                        <Space>
                            +8618221733710
                            <Tag color="green"></Tag>
                            <CheckableTag
                                checked={isModalPhoneVisible}
                                onChange={setIsModalPhoneVisible}
                            > 
                            </CheckableTag>
                        </Space>
                    </Space>
                </Col>
            </Row>
            <Divider/>
            <Modal getContainer={false} title="" visible={isModalUserVisible} onOk={handleOk}
                   onCancel={handleCancel}>
                <ul className={'modal-question'}>
                    <strong></strong>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
                <div className="modal-main">
                    <Space direction={'vertical'}>
                        <Space>
                            
                            <Tooltip color={'#2c97e8'} title="">
                                <Tag>required</Tag>
                            </Tooltip>
                            <Tooltip color={'#2c97e8'} title={''}>
                                <Tag>help?</Tag>
                            </Tooltip>
                        </Space>
                        <Radio.Group onChange={onChange} value={value}>
                            <Radio value={1}></Radio>
                            <Radio value={2}></Radio>
                        </Radio.Group>
                        <Row>
                            <Col span={12}>
                                <Form
                                    layout={'vertical'}
                                    name="basic"
                                    wrapperCol={{span: 22}}
                                    initialValues={{remember: true}}
                                    onFinish={onFinish}
                                    onFinishFailed={onFinishFailed}
                                >
                                    <Form.Item
                                        label={
                                            <Space>
                                                
                                                <Tooltip color={'#2c97e8'} title="">
                                                    <Tag>required</Tag>
                                                </Tooltip>
                                            </Space>}
                                        name="developerType"
                                    >
                                        <Input/>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                        name="eName"
                                    >
                                        <Input/>
                                    </Form.Item>
                                    <Form.Item
                                        label={
                                            <Space>
                                                
                                                <Tooltip color={'#2c97e8'} title="">
                                                    <Tag>required</Tag>
                                                </Tooltip>
                                            </Space>}
                                        name="InvoiceHeader"
                                    >
                                        <Input/>
                                    </Form.Item>
                                    <Form.Item
                                        label="QQ"
                                        name="qq"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Form>
                            </Col>
                            <Col span={12}>

                                <Form
                                    layout={'vertical'}
                                    name="basic"
                                    wrapperCol={{span: 22}}
                                    initialValues={{remember: true}}
                                    onFinish={onFinish}
                                    onFinishFailed={onFinishFailed}
                                >
                                    <Form.Item
                                        label={
                                            <Space>
                                                
                                                <Tooltip color={'#2c97e8'} title="">
                                                    <Tag>required</Tag>

                                                </Tooltip>
                                                <Tooltip color={'#2c97e8'} title="">
                                                    <Tag>help</Tag>
                                                </Tooltip>
                                            </Space>}
                                        name="AlternatePhoneNumber"
                                    >
                                        <Input/>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                        name="address"
                                    >
                                        <Input/>
                                    </Form.Item>
                                    <Form.Item
                                        label=""
                                        name="website"
                                    >
                                        <Input/>
                                    </Form.Item>
                                </Form>
                            </Col>
                        </Row>
                    </Space>
                </div>
            </Modal>

            <Modal  getContainer={false} title="" visible={isModalPhoneVisible} onOk={handleNext} onCancel={handleCancel}>
               <ul className={'modal-question'}>
                     
               </ul>
                <div className="modal-main">
                    <Search  allowClear
                             enterButton="" placeholder="input search text" onSearch={onSearch} style={{ width: 200 }} />
                </div>
            </Modal>
        </div>
    </>;
}
